/**
 * 容器内（单元格、高亮块、分栏）：
 * 第一个块级元素不要 margin-top
 * 最后一个块级元素不要 margin-bottom
 * 以保证单元格上下左右间距一致
 * 使用性能最好的 css 选择器，放弃分页下的支持
 */
export const blockMarginStyleInContainer = `
  & > [data-cangjie-edge-selectable='true']:first-child {
    margin-top: 0 !important;
  }

  & > [data-cangjie-edge-selectable='true']:last-child {
    margin-bottom: 0 !important;
  }

  & > [data-cangjie-leaf-block='true']:first-child {
    margin-top: 0 !important;
  }

  & > [data-cangjie-leaf-block='true']:last-child {
    margin-bottom: 0 !important;
  }
`;
